.sliderFS {
    color: $color-w;
    height: 100vh;

    &__arrow {
        position: absolute;
        bottom: 0;
        background-color: transparent;
        border: none;
        outline: none;
        cursor: pointer;
        font-size: 5rem;
        padding: 40px 40px;
        border: 1px solid $color-w;
        z-index: 100;
        display: flex;

        svg {
            .cls-1 {
                transition: all .4s ease-in;
            }
        }

        &--left {
            right: 50%;
        }

        &--right {
            left: 50%;
        }

        @include hfa {
            border-color: $color-brand;

            svg {
                .cls-1 {
                    fill: $color-brand;
                }
            }
        }
    }

    &>.slick-list {
        height: 100%;

        & .slick-track {
            height: 100%;
        }
    }
}

.slide {
    outline: none;
    text-align: center;
    position: relative;
    height: 100%;

    &__content {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        background: rgba(27, 31, 10, .6);
    }

    &__bg {
        min-height: 100%;
        min-width: 100%;
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        overflow: hidden;
    }

    &__text {
        font-size: 2.2rem;
        letter-spacing: 5px;
        padding: 30px 0;
        max-height: 280px;
        overflow: hidden;
    }

    &__autor {
        font-size: $fz-h3;
        text-transform: uppercase;
        margin: 0;

        span {
            letter-spacing: 5px;
        }
    }

    &__frase {
        font-size: $fz-h3;
        position: absolute;
        left: 0;
        bottom: 100px;
        transform: rotate(90deg);
        text-transform: uppercase;
        font-weight: 300;
        letter-spacing: 5px;

    }
}