.footer {
    text-align: center;
    padding-bottom: 50px;


    .container {
        display: flex;
        justify-content: space-between;
    }

    &__text {
        width: 30%;
        color: $color-brand;
        font-weight: 300;
        text-transform: uppercase;
        font-size: 1.4rem;
        letter-spacing: 5px;
        padding: 80px;
        border-top: 2px solid $color-brand;

        p {
            margin: 0;
            max-height: 100px;
            overflow: hidden;
        }
    }

    &__line {
        position: absolute;
        min-height: 50px;
        left: 50%;
        border-right: 2px solid $color-brand;
    }

    &__subscribe {
        width: 33%;
        position: relative;
    }

    &__copyright {
        text-transform: uppercase;
        letter-spacing: 5px;
        font-size: 1.1rem;
    }
}

.socials {
    padding: 0;
    list-style: none;
    margin: 20px;
    margin-top: 80px;
}

.social {
    display: inline-block;
    margin: 0 2px;

    &__link {
        display: inline-block;
        text-decoration: none;
        outline: none;
        color: $color-brand;
        width: 42px;
        height: 42px;
        line-height: 42px;
        border-radius: 50%;
        text-align: center;

        @include hfa {
            color: $color-w;
            background-color: $color-brand;
        }
    }
}

.form {
    padding: 70px 0 50px 0;

    &__title {
        text-transform: uppercase;
        letter-spacing: 4px;
        padding: 30px 0;
    }

    &__field {
        position: relative;
        display: flex;
        align-items: center;
    }

    &__input {
        padding: 20px 30px;
        color: $color-brand;
        flex-grow: 1;
        border: 2px solid $color-brand-light;
        border-right: none;
    }

    &__label {
        position: absolute;
        left: 30px;
        top: 50%;
        transform: translate(0, -50%);
        letter-spacing: 5px;
    }
}