.btn {
    text-decoration: none;
    outline: none;
    padding: 10px 30px;
    border: 1px solid #9db791;
    margin-bottom: 20px;
    font-size: 2rem;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    display: flex;
    align-items: center;

    @include hfa {
        background-color: $color-w;

        .line {
            background-color: $color-brand;
        }
    }

    &--brand {
        padding: 20px 40px;
        margin: 0;
        background-color: $color-brand;
        color: $color-w;
        text-transform: uppercase;
        border: 2px solid $color-brand;

        @include hfa {
            background-color: #f1f1f1;
            color: $color-brand;
        }
    }

    &--form {
        border: none;
        background-color: transparent;
        margin: 0;
        padding: 0 30px;
        height: 64px;
        border: 2px solid $color-brand-light;
        border-left: 0;
        font-size: 3rem;
        color: $color-brand;
        position: relative;
        cursor: pointer;

        @include hfa {
            background-color: $color-brand;
            color: $color-w;

            .cls-2 {
                fill: $color-w;
            }

            &:before {
                border-color: $color-brand;
            }
        }

        &:before {
            content: '';
            height: 80%;
            position: absolute;
            left: 0;
            top: 50%;
            transform: translate(0, -50%);
            border-right: 2px solid $color-brand-light;
            transition: all .4s ease-in;
        }
    }
}