@import 'normalize';
@import 'variables';
@import 'mixins';
@import 'buttons';
@import 'slick';



* {
    box-sizing: border-box;
}

html {
    height: 100%;
    font-size: 10px;
    scroll-behavior: smooth;
}

body {
    min-height: 100%;
    @include font($fz-main, $color-brand, 400, $text-ff, 2.2);
}

h2 {
    font-size: $fz-h2;
    text-transform: uppercase;
    margin: 0;
    letter-spacing: 5px;
    font-weight: 400;
    color: #1c1c1b;
}

h3 {
    font-size: $fz-h3;
    text-transform: uppercase;
    margin: 0;
    letter-spacing: 5px;
    font-weight: 400;
    color: #1c1c1b;
}

a {
    color: $color-w;
    text-decoration: none;
    outline: none;
    text-transform: uppercase;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

    @include hfa {
        color: $color-brand;
    }
}

input {
    background-color: transparent;
    border: none;
    outline: none;
}

label {
    text-transform: uppercase;
}

.container {
    max-width: 1070px;
    margin: 0 auto;

    &--accordion {
        max-width: 570px;
        height: auto;
    }

    &--medium {
        max-width: 830px;
    }
}

.section {
    padding: 100px 0;
    background-color: #f7f7f3;
}

.section+.section {
    padding-top: 0;
}

.line {
    margin-left: 20px;
    height: 2px;
    width: 45px;
    background-color: $color-w;
    transition: all .4s ease-in;
}

@import 'header';
@import 'section_whatWeAre';
@import 'section_products';
@import 'section_faqs';
@import 'section_sliderFullScreen';
@import 'footer';


@import 'media';