.header {
    height: 100vh;
    overflow: hidden;
    position: relative;
    color: $color-w;
    background: linear-gradient(rgba(0, 0, 0, .2) 0%, rgba(0, 0, 0, .2) 10%, transparent 20%), url(../img/header-lg.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);

    .container {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        height: 80%;
    }


    &__description {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
        padding: 0 50px;

        a {
            span {
                margin-right: 20px;
            }
        }
    }

    &__title {
        text-align: right;
        letter-spacing: 10px;
        font-size: 5.5rem;
        line-height: 1.3;
        max-width: 60%;
        margin: 0;
        text-transform: uppercase;
        text-shadow: 1px 1px 30px rgba(0, 0, 0, 0.6);
    }

    &__subtitle {
        font-weight: 300;
        font-size: 24px;
        letter-spacing: 5px;
        margin: 40px 0;
    }

    &__health {
        position: absolute;
        left: 0;
        bottom: 110px;
        transform: rotate(-90deg);
        text-transform: uppercase;
        display: flex;
        align-items: center;

        .line {
            margin-right: 20px;
            margin-left: 0;
            width: 100px;
        }
    }

}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 75px;
    padding: 0 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.6);
    position: relative;
}

.menu {
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    outline: none;
    padding: 0;
    margin: 0;

    &__item {
        position: relative;
        display: flex;
        align-items: center;
    }

    &__link {
        margin-right: 20px;
        text-decoration: none;
        outline: none;
        text-transform: uppercase;
        @include font($fz-menu, $color-w, 400, $text-ff, 1.6);
        position: relative;

        &:before {
            content: '';
            width: 120%;
            border-bottom: 2px solid #9cb891;
            position: absolute;
            left: -10%;
            top: 50%;
            opacity: 0;
            transition: all 0.4s ease-in-out;
        }

        @include hfa {
            color: $color-w;

            &::before {
                opacity: 1;
            }
        }

    }
}

.close {
    display: none;
}

.toggler {
    display: none;
    text-shadow: 1px 1px 30px rgba(0, 0, 0, 0.6);
}

.logo {
    font-size: 3rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    &--footer {
        font-size: 6rem;
        top: 0;
    }
}

.user {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    font-size: $fz-menu;

    a {
        height: 100%;
        padding: 0 20px;
        border-left: 1px solid rgba(255, 255, 255, 0.6);
        display: flex;
        align-items: center;

        &:last-of-type {
            padding-right: 0;
        }
    }
}

.togglerSearch {
    display: none;
}

.closeSearch {
    display: none;
}

.formSearch {
    margin-right: 30px;
    height: 100%;
    padding-left: 20px;
    border-left: 1px solid rgba(255, 255, 255, 0.6);
    display: flex;
    align-items: center;

    &__field {
        position: relative;
    }

    &__label {
        position: absolute;
        display: flex;
        align-items: center;
        left: 0;
        top: 50%;
        transform: translate(0, -50%);

        i {
            font-size: 16px;
            margin-right: 5px;
        }

        @include hfa {
            color: $color-brand;
        }
    }

    &__input {
        width: 80px;
        color: $color-w;
        transition: all 0.4s ease-in-out;
    }

}

.arrow {
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translate(-50%, -50%);
    z-index: 1;
    font-size: 2rem;
}