.products {
    text-align: center;

}

.slider {
    padding: 75px 0;
    overflow: hidden;

    .slick-slide {
        margin: 0 20px;
        outline: none;
    }

    .slick-list {
        margin: 0 -20px;
        padding-bottom: 50px;
    }

    &__arrow {
        position: absolute;
        bottom: 0;
        background-color: transparent;
        border: none;
        outline: none;
        cursor: pointer;
        padding: 20px 40px;

        svg {
            .cls-3 {
                transition: all .4s ease-in;
            }
        }

        &--left {

            right: 50%;
        }

        &--right {
            left: 50%;

        }

        @include hfa {
            color: $color-main;

            svg {
                .cls-3 {
                    fill: $color-brand-light;
                }
            }
        }
    }

}



.product {
    display: flex;
    flex-direction: column;
    justify-content: center;

    &__description {
        background-color: #fff;
        padding: 40px 100px;
        border: 1px solid $color-brand-light;
    }

    &__title {
        max-height: 35px;
        overflow: hidden;
        letter-spacing: 2px;
        color: #1c1c1b;
    }

    &__img {
        position: relative;
        overflow: hidden;
        height: 300px;
        width: 100%;

        img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }
}

.stars {
    font-size: 1.6rem;

    &--light {
        color: #a9b383;
    }
}

.price {
    display: flex;

    &__quantity {
        flex-grow: 1;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding-left: 20px;
        background-color: #f1f1f1;
        border-left: 1px solid #e1e1e1;
        border-bottom: 1px solid #e1e1e1;
    }

}