.whatWeAre {
    text-align: center;

    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

}

.sponsors {
    border: 30px solid $color-brand-light;
    border-image: url(../img/Holder.png) 30 round round;
    padding: 30px 60px;

    &__title {
        padding: 25px;
        padding-top: 0;
    }

    &__img {
        max-width: 100%;
        max-height: 100%;
    }
}


.tabs {
    width: 100%;
    margin: 100px;
    margin-bottom: 0;
    padding: 0 20px;

    &__wrap {
        display: flex;
        justify-content: flex-start;
    }

    &__link {
        text-align: center;
        padding: 20px 50px 0 0;
        cursor: pointer;
        border-bottom: 2px solid $color-brand-light;
        transition: all 0.4s ease-in;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        font-size: $fz-h2;
        text-transform: uppercase;
        margin: 0;
        letter-spacing: 5px;
        font-weight: 400;
        color: #1c1c1b;

        &.active {
            border-bottom: 2px solid $color-main;
            color: $color-main;
        }
    }

    &__content {
        display: none;
        min-height: 700px;
    }

    &__content.active {
        display: block;
    }

}


.howItWorks {
    display: flex;
    text-align: left;
    padding-top: 75px;
}

.article {
    min-width: 400px;
    max-height: 600px;
    overflow: hidden;

    &__title {
        margin: 30px 0;
    }

    &__text {
        margin-bottom: 40px;
        color: $color-brand;
        font-weight: 300;
    }
}

.images {
    position: relative;
    right: -200px;
    width: 100%;

    &__wrap {
        position: relative;
        overflow: hidden;
        max-width: 570px;
        width: 570px;
        height: 300px;
        overflow: hidden;
        margin-bottom: 30px;

        img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }
}