@media screen and (max-width: 1400px) {

    .images {
        right: 0;
        margin-left: 20px;
    }
}

@media screen and (max-width: 1200px) {

    .header {
        background: linear-gradient(rgba(0, 0, 0, .2) 0%, rgba(0, 0, 0, .2) 10%, transparent 20%), url(../img/header-md.jpg);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }

    .images {
        flex-basis: 50%;
    }
}

@media screen and (max-width: 992px) {

    .header {
        &__title {
            font-size: 4rem;
        }

        &__subtitle {
            margin: 20px 0;
        }
    }

    .nav {
        justify-content: flex-end;
    }

    .menu {
        position: fixed;
        top: 0;
        right: 0;
        margin: 0;
        transform: translate(0, -100%);

        &--opened+.menu {
            position: fixed;
            margin: 0;
            top: 0;
            right: 0;
            transform: translate(0, 0);
            z-index: 1000;
            flex-direction: column;
            transition: all .3s ease-in-out;
            width: 100%;
            height: 100vh;
            text-align: center;
            background-color: rgba(0, 0, 0, .8);
            justify-content: space-evenly;

            & .menu__link {
                font-size: 4rem;
                color: $color-w;
                width: 100%;
                text-align: center;

                @include hfa {
                    color: $color-brand;
                }

                &:before {
                    display: none;
                }
            }

            & .close {
                display: block;
                position: absolute;
                top: 20px;
                right: 20px;
                font-size: 3rem;
                color: #fff;
            }
        }
    }

    .toggler {
        display: block;
        position: fixed;
        left: 30px;
        top: 5px;
        font-size: 3rem;
        z-index: 100;

    }


    .howItWorks {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .tabs {
        &__wrap {
            justify-content: center;
        }

        &__link {
            padding: 20px 20px 5px 20px;
        }
    }

    .images {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .slide {
        &__frase {
            left: -50px;
        }
    }
}



@media screen and (max-width: 768px) {

    .section {
        padding: 50px 0;
    }

    .header {
        background: linear-gradient(rgba(0, 0, 0, .2) 0%, rgba(0, 0, 0, .2) 10%, transparent 20%), url(../img/header-sm.jpg);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        &__title {
            font-size: 3rem;
        }

        &__subtitle {
            font-size: 2rem;
        }

        &__health {
            left: -60px;
        }
    }

    .tabs {
        padding: 0;

        &__link {
            display: flex;
            align-items: center;
            line-height: 1.2;
            font-size: 2.6rem;
        }
    }

    .howItWorks {
        padding: 25px;
    }


    .user {
        .formSearch {
            display: none;

            &.opened {
                display: inline-block;
                position: absolute;
                width: 40%;
                height: 40px;
                top: 75px;
                right: 0;
                padding: 0;
                margin: 0;
                border: none;
                border-bottom: 1px solid $color-brand-light;
                border-left: 1px solid $color-brand-light;

                .formSearch__input {
                    width: 100% !important;
                    padding-left: 10px;

                    &::placeholder {
                        padding-left: 10px;
                        color: $color-w;
                        letter-spacing: 1px;
                    }
                }

                .formSearch__label {
                    padding-left: 10px;
                }

                i {
                    display: none;
                }
            }
        }
    }


    .togglerSearch {
        display: inline-block;
        padding: 20px;
        font-size: 2.2rem;
    }

    .slide {

        &__frase {
            left: -50px;
        }
    }

    .footer {
        .container {
            flex-direction: column;
            align-items: center;
        }

        &__text {
            width: 100%;
            padding: 20px;

            &:last-child {
                border-bottom: 2px solid $color-brand;
            }
        }

        &__subscribe {
            width: 100%;
            order: 1;
            margin-top: 40px;
        }

        .form {
            &__field {
                width: 60%;
                margin: 0 auto;
            }
        }
    }

}

@media screen and (max-width: 576px) {
    h2 {
        font-size: 2.4rem;
    }

    h3 {
        font-size: 2rem;
    }

    .images {
        &__wrap {
            width: 90%;
        }
    }

    .slider {
        padding: 0;
    }

    .header {

        &__description {
            align-items: center;
        }

        &__title {
            text-align: center;
        }

        .user {
            flex-direction: column;

            a {
                padding: 0 10px;
                min-width: 80px;
                border-right: 1px solid rgba(255, 255, 255, 0.6);
                display: flex;
                justify-content: center;
            }
        }

        &__title {
            max-width: 100%;
            font-size: 3rem;
        }

        &__subtitle {
            margin: 10px 0;
            font-size: 2rem;
        }

        &__health {
            display: none;
        }

        .arrow {
            bottom: 0;
        }

        .nav {
            padding: 0;
        }

    }

    .tabs {

        &__link {
            padding-right: 10px;
            padding-left: 10px;
            font-size: 2rem;
        }
    }

    .article {
        min-width: auto;
        max-height: fit-content;
    }

    .accordion {
        &__link {
            i {
                left: 10px;
            }
        }
    }

    .sliderFS {
        &__arrow {
            padding: 20px 40px;
        }
    }

    .slide {
        &__text {
            font-size: 1.6rem;
            padding: 10px;
            max-height: 190px;
        }

        &__autor {
            font-size: 1.8rem;
        }

        &__frase {
            display: none;
        }
    }

    .logo {

        &--footer {
            font-size: 4rem;
        }
    }

    .socials {
        margin-top: 40px;
    }

    .footer {

        .form {
            &__field {
                width: 100%;
            }
        }
    }
}


@media screen and (max-width: 326px) {

    h2 {
        font-size: 2rem;
    }

    h3 {
        font-size: 1.6rem;
    }

    .btn {
        padding: 5px 15px;
        font-size: 1.6rem;

        &--form {
            padding: 0 30px;
        }
    }

    a {
        font-size: 1.4rem;
    }

    .header {

        &__description {
            padding: 0 10px;
            text-align: center;
        }

        &__title {
            font-size: 2rem;
        }

        .logo {
            display: none;
        }

        .user {
            .formSearch {
                &.opened {
                    width: 100%;
                }
            }
        }
    }

    .sponsors {
        padding: 0;

        &__title {
            padding: 0 0 10px 0;
            font-size: 1.6rem;
        }
    }

    .tabs {
        margin: 0;
        padding: 0 10px;

        &__link {
            padding-right: 5px;
            padding-left: 5px;
            font-size: 1.6rem;
            letter-spacing: 1px;
        }
    }

    .howItWorks {
        padding: 0;
    }

    .sliderFS {
        &__arrow {
            padding: 10px 20px;
            font-size: 3rem;
        }
    }


    .article {
        &__title {
            margin: 5px 0;
        }
    }

    .images {
        margin: 0;

        &__wrap {
            width: 90%;
        }
    }

    .product {

        &__description {
            padding: 10px;
        }
    }

    .menu {
        &--opened+.menu {
            & .menu__link {
                font-size: 2.4rem;
            }
        }
    }
}