.faqs {
    &__title {
        text-align: center;
        margin-bottom: 40px;
    }
}

.accordion {
    width: 100%;
    letter-spacing: 2px;
    font-weight: 300;

    &__item {
        border-top: 1px solid $color-brand-light;

        &:last-child {
            border-bottom: 1px solid $color-brand-light;

        }
    }

    &__link {
        cursor: pointer;
        padding: 25px 50px;
        color: $color-brand;
        position: relative;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

        i {
            position: absolute;
            left: 0;
            top: 50%;
            transform: translate(0, -50%);
        }

        @include hfa {
            background-color: none;
        }
    }

    &__content {
        padding-left: 50px;
        padding-bottom: 20px;
        display: none;
        color: #1c1c1b;
    }
}